/* General body styling for dark theme */
body {
  font-family: sans-serif;
  margin: 0;
  background-color: #121212; /* Dark background */
  color: #e0e0e0; /* Light text */
}

.container {
  max-width: 900px;
  margin: auto;
  padding: 1rem;
}

/* Headings */
h1 {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 1rem;
  color: #ffffff;
}

/* Form & Filter Panel */
form,
.filter-panel {
  background: #1e1e1e; /* Slightly lighter dark */
  padding: 1rem;
  border-radius: 10px;
  box-shadow: 0 0 5px rgba(255, 255, 255, 0.05);
  margin-bottom: 1rem;
}

/* Inputs and buttons */
input,
select,
button {
  width: 100%;
  padding: 0.5rem;
  margin-top: 0.2rem;
  border-radius: 6px;
  border: 1px solid #444;
  font-size: 1rem;
  background-color: #2c2c2c;
  color: #e0e0e0;
}

/* Buttons */
button {
  background-color: #3b82f6;
  color: white;
  border: none;
  cursor: pointer;
  transition: background 0.2s ease;
}

button:hover {
  background-color: #2563eb;
}

/* Gallery grid */
.card-gallery {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 1rem;
}

/* Card styling */
.card {
  background: #1e1e1e;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 0 8px rgba(255, 255, 255, 0.05);
  padding: 0.5rem;
  text-align: center;
}

.card img {
  max-width: 100%;
  border-radius: 6px;
}

/* Tags and details */
.card h3 {
  margin: 0.5rem 0;
  color: #fff;
}

.card p {
  font-size: 0.9rem;
  margin: 0.2rem 0;
  color: #ccc;
}

.card .tags {
  font-size: 0.85rem;
  color: #aaa;
}

.card .actions {
  margin-top: 0.5rem;
}

.card .delete-btn {
  color: #f87171;
  font-size: 0.8rem;
  cursor: pointer;
  text-decoration: underline;
}

/* Modal */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.85);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

.modal-content {
  position: relative;
  background: #1e1e1e;
  padding: 1rem;
  border-radius: 10px;
  max-width: 90vw;
  max-height: 90vh;
  overflow: auto;
  box-shadow: 0 0 15px rgba(255, 255, 255, 0.1);
}

.modal-content img {
  max-width: 100%;
  max-height: 80vh;
  display: block;
  margin: 0 auto;
  border-radius: 8px;
}

.close-btn {
  margin-top: 0.8rem;
  background: #ef4444;
  color: white;
  border: none;
  padding: 0.4rem 1rem;
  font-size: 1rem;
  border-radius: 6px;
  cursor: pointer;
  display: block;
  margin-left: auto;
}

.close-btn:hover {
  background: #dc2626;
}
